import { r as redirect } from "../../../../../chunks/index.js";
const POST = async (event) => {
  const { locals: { supabase } } = event;
  const userFetch = await supabase.auth.getUser();
  if (!userFetch.data.user)
    ;
  const { error } = await supabase.auth.signOut();
  if (error)
    ;
  else {
    redirect(303, "/");
  }
};
export {
  POST
};
